<style scoped>
.contain {
  width: 1386px;
  margin: 36px auto 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contain_l {
  width: 300px;
  margin-bottom: 24px;
}
.contain_ltop img {
  width: 300px;
  border-radius: 16px;
}
.contain_lbottom {
  padding: 0 12px;
}
.contain_lbottom h5 {
  font-size: 16px;
  font-weight: 700;
  color: rgb(7, 17, 27);
}
.contain_lbottom p {
  display: flex;
  justify-content: space-between;
  font-size: 12px;
  font-weight: 400;
  color: rgb(77, 85, 93);
  line-height: 20px;
  margin: 4px 0;
}
.contain_lbottom p:nth-child(4) > span:nth-child(1) {
  color: rgb(240, 20, 20);
}
.contain_m {
  width: 100%;
  text-align: center;
  margin-bottom: 24px;
}
</style>
<template>
  <div class="contain">
    <div class="contain_m">
      <Page
        :total="total"
        @on-change="change"
        :page-size="pagesize"
        show-total
        show-elevator
      />
    </div>
    <div class="contain_l" v-for="(item, index) in contain" :key="index">
      <div class="contain_ltop">
        <img :src="item.cover_src" alt />
      </div>
      <div class="contain_lbottom">
        <h5 @click="topage(item.id)">{{ item.title }}</h5>
        <p>
          <span>观看数:{{ item.watch }}</span>
          <span>{{ item.comment }}</span>
        </p>
        <p>{{ item.introduce }}</p>
        <p>
          <span>{{ item.money }}元:</span>
          <span
            @click="toshop(item.id, item.title, item.cover_src, item.money)"
            >{{ item.shop }}</span
          >
        </p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      contain: [],
      page: 1,
      pagesize: 24,
      total: 0,
      url: "http://127.0.0.1:3000/api/ganclassifyall"
      // id:this.$router.history.current.params.id,
      // sid:this.$router.history.current.params.sid,
    };
  },
  methods: {
    //去详情
    topage(ids){

      this.$router.push({ name: 'page', params: { id: ids }})
    },
    //分页切换
    change(page) {
      // window.console.log("page" + this.url);
      this.$axios({
        url: this.url,
        params: {
          page: page,
          pagesize: this.pagesize,
          id: this.$router.history.current.params.id
        }
      }).then(res => {
        this.contain = res.data.data;
        this.total = res.data.total;
      });
    },
    gancontent() {
      //查询分类对应内容
      if (this.$router.history.current.params.id > 0) {
        this.$axios({
          url: "http://127.0.0.1:3000/api/containall",
          params: {
            page: this.page,
            pagesize: this.pagesize,
            id: this.$router.history.current.params.id
          }
        }).then(res => {
          this.contain = res.data.data;
          this.total = res.data.total;
          this.url = "http://127.0.0.1:3000/api/containall";
          // window.console.log(this.url);
        });
      } else {
        this.$axios({
          url: "http://127.0.0.1:3000/api/ganclassifyall",
          params: {
            page: this.page,
            pagesize: this.pagesize
          }
        }).then(res => {
          this.contain = res.data.data;
          this.total = res.data.total;
          this.url = "http://127.0.0.1:3000/api/ganclassifyall";
        });
      }
    },
    subclassification() {
      //查询分类对应子分类对应内容
      if (this.$router.history.current.params.gid > 0) {
       
        this.$axios({
          url: "http://127.0.0.1:3000/api/subclassification",
          params: {
            page: this.page,
            pagesize: this.pagesize,
            id: this.$router.history.current.params.gid
          }
        }).then(res => {
          this.contain = res.data.data;
          this.total = res.data.total;
        });
      }
    },
    toshop(id, title, pic, money) {
      if (window.sessionStorage.getItem("user") == null) {
        return alert("请先登录");
      }
      this.$axios({
        method: "POST",
        url: "http://127.0.0.1:3000/api/toshop",
        params: {
          pid: id,
          title: title,
          pic: pic,
          money: money,
          uid: JSON.parse(window.sessionStorage.getItem("user")).id
        }
      }).then(res => {
        // window.console.log(res.data)
        if (res.data.data.affectedRows == 1) {
          alert("成功加入购物车");
        } else if (res.data.like.msg) {
          alert("已经加入不要在点");
        } else {
          alert("加入失败");
        }
      });
    }
  },
  mounted() {
    this.gancontent();
    this.subclassification();
  },
  watch: {
    "$route.path"() {
      if (this.$router.history.current.params.gid > 0) {
        this.subclassification();
      } else {
        this.gancontent();
      }
    
    }
  }
};
</script>
